.header {
  position: relative;
  z-index: 10;
  padding: 18px 0;
  &:after {
    content: '';
    position: absolute;
    z-index: 4;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: rgba(60, 62, 71, 0.15);
    pointer-events: none;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
  }
  &__logo {
    position: relative;
    z-index: 4;
    width: 138px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 32px;
    li a {
      font-size: 14px;
      line-height: 115%;
      font-weight: 500;
      color: #555B71;
      &:hover {
        color: #36317B;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__btn {
    height: 40px;
    padding: 5px 20px;
    span {
      font-size: 14px;
    }
  }
  &__mob {
    position: relative;
    z-index: 3;
    display: none;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
}
.burger {
  position: relative;
  z-index: 10;
  width: 20px;
  height: 18px;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #36317B;
    border-radius: 3px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 8px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 8px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 0;
    &__wrap {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      text-align: center;
      gap: 80px;
      background: #fff;
      padding: 112px 15px 40px 15px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .header__logo {
        display: none;
      }
    }
    &__list {
      width: 100%;
      flex-direction: column;
      gap: 80px;
      li {
        position: relative;
        width: 100%;
        &:not(:last-child) {
          &:after {
            content: '';
            position: absolute;
            left: 50%;
            bottom: -40px;
            transform: translate(-50%, 0);
            width: calc(100% + 30px);
            height: 1px;
            background: rgba(0, 0, 0, 0.1);
          }
        }
      }
    }
    &__mob {
      display: flex;
      padding: 15px 0;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% + 30px);
        height: 100%;
        background: #fff;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__wrap {
      padding-top: 100px;
    }
    &__logo {
      width: 108px;
    }
    &__btns {
      width: 100%;
      flex-direction: column;
    }
    &__btn {
      width: 100%;
    }
  }
}