.main {
  padding: 60px 0 30px;
  overflow: hidden;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 45px;
    padding-left: max(30px, calc(100% - 1320px) / 2);
  }
  &__desc {
    position: relative;
    z-index: 2;
    width: 50%;
    max-width: 650px;
  }
  &__img {
    position: relative;
    width: 50%;
    img {
      position: relative;
      z-index: 2;
      width: 100%;
    }
  }
  &__subtitle {
    position: relative;
    font-size: 20px;
    line-height: 115%;
    color: #36317B;
    padding: 10px 0 10px 20px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 2px;
      height: calc(100% - 10px);
      background: #03045E;
      border-radius: 3px;
    }
  }
  &__title {
    font-size: 60px;
    line-height: 100%;
    font-weight: 700;
    margin-top: 24px;
  }
  &__nav {
    margin-top: 34px;
  }
  &__subtitle2 {
    font-size: 20px;
    line-height: 115%;
    font-weight: 600;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 2px;
    margin-top: 20px;
    li {
      display: flex;
      align-items: center;
      gap: 22px;
      background: #F6F7FB;
      border-radius: 12px;
      padding: 17.5px 22px;
      i {
        width: 20px;
        flex-shrink: 0;
        img {
          max-width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 115%;
      }
    }
  }
  .circle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50);
    width: 164px;
    height: 164px;
    background: #0BAE5F;
    filter: blur(75px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    top: -92px;
    right: -92px;
    width: 244px;
    height: 244px;
    background: #0BAE5F;
    filter: blur(170px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    right: -92px;
    bottom: -92px;
    width: 244px;
    height: 244px;
    background: #0BAE5F;
    filter: blur(170px);
    border-radius: 50%;
    pointer-events: none;
  }
  &__wrap2 {
    position: relative;
    z-index: 2;
    background: #fff;
    border-top: 1px solid rgba(60, 62, 71, 0.15);
    border-bottom: 1px solid rgba(60, 62, 71, 0.15);
    margin-top: 16px;
  }
  &__wrap2-inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__group {
    position: relative;
    max-width: 372px;
    width: 100%;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    padding: 22px 60px 22px 0;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 100%;
      background: rgba(60, 62, 71, 0.15);
      pointer-events: none;
    }
  }
  &__btn {
    height: 56px;
    padding: 5px 10px 5px 20px;
    margin-top: 28px;
    span {
      font-size: 18px;
    }
    .btn-logo {
      width: 102px;
      img {
        width: 100%;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    padding: 22px 0 22px 60px;
  }
  .green-card {
    background: linear-gradient(90.00deg, rgb(11, 174, 95),rgb(11, 174, 95) 100%);
  }
  .blue-card {
    background: rgb(54, 49, 123);
  }
}
.main-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  border-radius: 16px;
  padding: 24px;
  color: #fff;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: -5px;
    left: -10px;
    width: 100px;
    height: 100px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgb(255, 255, 255),rgba(255, 255, 255, 0) 100%);
    filter: blur(27.5px);
    border-radius: 50%;
    opacity: 0.3;
    pointer-events: none;
  }
  &__title {
    position: relative;
    z-index: 2;
    min-height: 56px;
    display: flex;
    align-items: center;
    font-size: 24px;
    line-height: 115%;
    font-weight: 700;
    margin-bottom: auto;
  }
  &__text {
    font-size: 14px;
    line-height: 115%;
    font-weight: 500;
    margin-top: 20px;
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
}
.info {
  padding: 30px 0 100px;
  &__title {
    max-width: 880px;
    text-align: center;
    margin: 0 auto;
  }
  &__items {
    display: grid;
