    }
  }
  &__btn {
    height: 55px;
    padding: 5px 24px;
    margin: 60px auto 0;
    span {
      font-size: 18px;
    }
  }
}
.advantages {
  background: #F5F7FB;
  padding: 90px 0;
  &__top {
