      color: #36317B;
    }
  }
  &__cards {
    display: none;
  }
  &__table {
    width: calc(100% + 10px);
    border-collapse: collapse;
    table-layout: fixed;
    tbody {
      tr {
        &:nth-child(1) {
          border-top: unset;
          td:nth-child(2),
          td:nth-child(3),
          td:nth-child(4) {
            &:after {
              border-top: 1px solid #DBDCDE;
              border-radius: 16px 16px 0 0;
            }
          }
          td {
            &:before {
              display: none;
            }
          }
        }
        &:last-child {
          border-top: unset;
          td:nth-child(2),
          td:nth-child(3),
          td:nth-child(4) {
            &:after {
              border-bottom: 1px solid #DBDCDE;
              border-radius: 0 0 16px 16px;
            }
          }
        }
        &:nth-last-child(-n+2) {
          td:before {
            display: none;
          }
        }
        td:last-child {
          &:before {
            left: -10px;
          }
        }
      }
      tr td {
        position: relative;
        padding: 24px 30px;
        &:nth-child(1) {
          width: 440px;
          background: #fff;
          &:after {
            display: none;
          }
        }
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%, 0);
          width: calc(100% - 20px);
          height: 100%;
          background: #F5F7FB;
          border-left: 1px solid #DBDCDE;
          border-right: 1px solid #DBDCDE;
          pointer-events: none;
        }
        &:before {
          content: '';
          position: absolute;
          z-index: 1;
          top: 0;
          left: 0;
          width: 100%;
          height: 1px;
          background: #dbdcde;
          pointer-events: none;
        }
      }
