    &__cards {
      grid-template-columns: 100%;
    }
  }
  .advantages-card2 {
    &:after,
    &:before {
      display: none;
    }
  }
  .info2 {
    &__inner {
      flex-direction: column;
    }
    &__img {
      width: 100%;
      border-radius: 16px 16px 0 0;
    }
    &__desc {
      width: 100%;
    }
  }
  .calculator {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
      border-radius: 16px 16px 0 0;
    }
  }
  .advantages2 {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
    }
  }
  .info3 {
    &__wrap {
      grid-template-columns: 100%;
    }
  }
  .stages {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
  }
  .tariffs {
    &__table {
      display: none;
    }
    &__cards {
      display: grid;
      grid-template-columns: 100%;
      gap: 30px;
      margin-top: 30px;
    }
  }
  .tariff-card {
    background: #F5F7FB;
    border: 1px solid #DBDCDE;
    border-radius: 16px;
    padding: 20px;
    &__title {
      margin-bottom: 20px;
    }
    &__text {
      margin-bottom: 20px;
    }
    &__price-wrap {
      padding: 20px 0;
    }
    &__subscribe {
      position: relative;
      background: transparent;
      padding: 20px 0;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% + 40px);
        height: 100%;
        background: #36317b;
        pointer-events: none;
      }
    }
    &__subscribe-text,
    &__subscribe-price {
      position: relative;
      z-index: 2;
    }
    &__btn {
      margin-top: 20px;
    }
  }
  .cases {
    padding-bottom: 106px;
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 46px;
      transform: translate(-50%, 0);
    }
  }
  .questions {
    &__inner {
      flex-direction: column;
      gap: 24px;
    }
    &__group {
      width: 100%;
    }
    &__items {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-top: 25px;
    &__subtitle {
      font-size: 14px;
    }
    &__title {
      font-size: 28px;
    }
    &__subtitle2 {
      font-size: 14px;
    }
    &__list {
      margin-top: 15px;
      li {
        gap: 14px;
        padding: 14px;
        span {
          font-size: 14px;
        }
      }
    }
    &__btn {
      width: 100%;
      height: 48px;
      span {
        font-size: 14px;
      }
    }
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .info {
    &__title {
      text-align: start;
    }
    &__item {
      padding: 10px 10px 24px 10px;
      &-img {
        height: 110px;
      }
      &-title {
        font-size: 14px;
      }
    }
    &__btn {
      width: 100%;
    }
  }
  .advantages-card {
    padding: 20px 16px;
    &__img {
      width: 60px;
      &:not(:first-child) {
        margin-left: -10px;
      }
    }
  }
  .advantages-card2 {
    .advantages-card__wrap {
      padding: 20px 16px;
    }
  }
  .info2 {
    &__desc {
      padding: 24px 16px;
    }
    &__btn {
      width: 100%;
    }
  }
  .calculator {
    &__left {
      padding: 24px 16px;
    }
    &__right {
      padding: 24px 16px;
    }
  }
  .advantages2 {
    &__cards {
      grid-template-columns: 100%;
    }
    &__info {
      justify-content: center;
      text-align: center;
    }
    .circle,
    .circle2 {
      display: none;
    }
    .circle3 {
      left: unset;
      right: -60px;
    }
    .circle4 {
      top: unset;
      left: 20px;
      bottom: -60px;
    }
  }
  .info3 {
    &__title {
      text-align: start;
    }
    &__left {
      padding: 20px 10px;
    }
    &__group {
      width: 100%;
      flex-direction: column;
      align-items: center;
      gap: 30px;
    }
    &__img {
      width: 190px;
    }
    &__text2 {
      gap: 20px;
      p {
        &:nth-child(1) {
          order: 2;
        }
        &:after {
          display: none !important;
        }
      }
    }
  }
  .info4 {
    &__inner {
      align-items: start;
      text-align: start;
      padding: 24px 16px;
    }
    &__btn {
      width: 100%;
    }
  }
  .stages {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .stage-card {
    padding: 20px;
  }
  .tariffs-banner {
    &__inner {
      flex-direction: column;
      gap: 20px;
      padding: 20px 16px;
    }
    &__img {
      position: static;
      transform: unset;
      max-width: 260px;
      width: 100%;
      order: -1;
    }
    &__img2 {
      display: none;
    }
  }
  .cases {
    &__btn {
      width: 100%;
    }
  }
  .case-card {
    padding: 24px 16px;
  }
}

@media screen and (max-width: 450px) {
  .calculator {
    &__subtitle {
      font-size: 14px;
    }
    &__form {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
      gap: 12px;
      margin-top: 16px;
      .form-group__title {
        font-size: 10px;
      }
      &-group {
        input {
          height: 25px;
          font-size: 10px;
          padding: 0 10px;
        }
      }
      &-checkboxes {
        grid-template-columns: repeat(4, calc(25% - 12px / 4));
        gap: 4px;
      }
      &-checkbox {
        label {
          height: 25px;
          gap: 5px;
          font-size: 10px;
          i {
            width: 10px;
          }
        }
      }
      &-text {
        font-size: 14px;
      }
    }
  }
}