.footer {
  position: relative;
  background: #000;
  border-radius: 16px 16px 0 0;
  color: #fff;
  overflow: hidden;
  &__top {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    padding: 30px 0;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 50px;
  }
  &__logo {
    width: 136px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 16px;
    li {
      font-size: 14px;
      line-height: 115%;
      font-weight: 500;
    }
    li a {
      &:hover {
        color: #36317B;
      }
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 50px;
  }
  &__btn {
    height: 40px;
    background: transparent;
    border: 1px solid #fff;
    border-radius: 100px;
    padding: 5px 20px;
    color: #fff;
    &:hover {
      background: #fff;
      color: #111;
    }
    span {
      font-size: 14px;
      line-height: 115%;
      font-weight: 500;
    }
  }
  &__bottom {
    position: relative;
    z-index: 2;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding: 30px 0;
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 10px 16px;
    p {
      font-size: 12px;
      line-height: 115%;
    }
    p a {
      &:hover {
        color: #36317B;
      }
    }
  }
  &__img {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    max-width: 898px;
    width: 100%;
    padding: 0 24px;
    img {
      position: relative;
      z-index: 1;
      max-width: 100%;
      max-height: 100%;
    }
  }
  .circle {
    position: absolute;
    left: 80px;
    bottom: -60px;
    width: 116px;
    height: 116px;
    background: #0BAE5F;
    filter: blur(100px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    left: 40%;
    bottom: -50px;
    transform: translate(-50%, 0);
    width: 116px;
    height: 116px;
    background: #060794;
    filter: blur(100px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    right: 80px;
    bottom: -60px;
    width: 116px;
    height: 116px;
    background: #0BAE5F;
    filter: blur(100px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle4 {
    position: absolute;
    right: 40%;
    bottom: -50px;
    transform: translate(50%, 0);
    width: 116px;
    height: 116px;
    background: #060794;
    filter: blur(100px);
    border-radius: 50%;
    pointer-events: none;
  }
}

@media screen and (max-width: 1350px) {
  .footer {
    &__group {
      gap: 32px;
    }
    &__group2 {
      gap: 32px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    &__top {
      flex-direction: column;
      gap: 24px;
    }
    &__group {
      flex-direction: column;
      gap: 24px;
    }
    &__group2 {
      flex-direction: column;
      gap: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__top {
      align-items: start;
      padding: 24px 0;
    }
    &__group {
      width: 100%;
      align-items: start;
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      padding-bottom: 24px;
    }
    &__logo {
      width: 112px;
    }
    &__list {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      li {
        font-size: 12px;
      }
    }
    &__bottom {
      align-items: start;
      padding: 24px 0;
    }
    &__group2 {
      width: 100%;
      align-items: start;
    }
    &__btn {
      width: 100%;
    }
  }
}