.btn-blue__outline {
  background: #fff;
  border: 1px solid #36317B;
  border-radius: 100px;
  color: #36317B;
  &:hover {
    background: #36317B;
    color: #fff;
  }
  span {
    line-height: 115%;
    font-weight: 500;
  }
}
.btn-blue {
  gap: 14px;
  background: #36317B;
  border: 1px solid transparent;
  border-radius: 100px;
  color: #fff;
  &:hover {
    background: #fff;
    border-color: #36317B;
    color: #36317B;
  }
  span {
    line-height: 115%;
    font-weight: 500;
  }
  .btn-logo {
    display: flex;
    align-items: center;
  }
}
.h2 {
  font-size: 40px;
  line-height: 115%;
  font-weight: 700;
  .title-logo {
    position: relative;
    width: 190px;
    min-height: 16px;
    display: inline-flex;
    img {
      position: absolute;
      left: 0;
      bottom: -10px;
      width: 100%;
    }
  }
  .title-line {
    position: relative;
    display: inline-block;
    img {
      position: absolute;
      top: 0;
      left: 0;
      max-width: 120%;
      pointer-events: none;
      img {
        max-width: 100%;
      }
    }
  }
}
.h3 {
  font-size: 26px;
  line-height: 115%;
  font-weight: 700;
  .title-logo {
    position: relative;
    width: 120px;
    min-height: 16px;
    display: inline-flex;
    img {
      position: absolute;
      left: 0;
      bottom: -7px;
      width: 100%;
    }
  }
  .title-line {
    position: relative;
    display: inline-block;
    img {
      position: absolute;
      top: 0;
      left: 0;
      max-width: 120%;
      pointer-events: none;
      img {
        max-width: 100%;
      }
    }
  }
}
.form {
  &-group__title {
    display: block;
    line-height: 115%;
    font-weight: 500;
    color: #555B71;
    margin-bottom: 6px;
  }
  &-group {
    input {
      width: 100%;
      background: #fff;
      border: 1px solid rgba(85, 91, 113, 0.2);
      border-radius: 100px;
      padding: 0 20px;
      line-height: 115%;
      font-weight: 500;
    }
  }
  &-checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100% !important;
      height: 100% !important;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: #36317B;
      color: #fff;
      i {
        display: flex;
      }
    }
    label {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      background: #fff;
      border: 1px solid rgba(85, 91, 113, 0.2);
      border-radius: 100px;
      font-size: 14px;
      line-height: 115%;
      font-weight: 500;
      i {
        width: 16px;
        flex-shrink: 0;
        display: none;
        align-items: center;
        img {
          width: 100%;
        }
      }
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border: 1px solid #36317B;
  border-radius: 50%;
  &:hover {
    background: #36317B;
    svg path {
      fill: #fff;
    }
  }
  &:after {
    display: none;
  }
  svg {
    max-width: 80%;
    max-height: 80%;
  }
}
.swiper-pagination {
  z-index: 1;
  &-bullet {
    background: #03045E !important;
    opacity: 0.3;
    &-active {
      opacity: 1;
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(1, 0, 30, 0.6);
  backdrop-filter: blur(10px);
  padding: 0 15px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    top: 26px;
    right: 26px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        fill: #111;
      }
    }
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
}
.payment-modal {
  .modal__dialog {
    max-width: 610px;
    background: #ffffff;
    border-radius: 16px;
    padding: 40px;
  }
  .modal__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__img {
    width: 195px;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-top: 50px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 24px;
    p {
      font-size: 16px;
      line-height: 115%;
      color: #555B71;
      strong {
        font-weight: 600;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 28px;
    .title-logo {
      width: 120px;
      img {
        bottom: -5px;
      }
    }
  }
  .h3 {
    font-size: 20px;
  }
}

@media screen and (max-width: 576px) {
  .payment-modal {
    .modal__dialog {
      padding: 24px 20px;
    }
    &__img {
      width: 150px;
    }
    &__title {
      margin-top: 32px;
    }
    &__text {
      gap: 8px;
      margin-top: 16px;
      p {
        font-size: 14px;
      }
    }
  }
}